#home {
  height: 100vh;
  width: 100%;
  margin:2rem;
}
.home {
  width: 100%;  
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5rem;
  color: #31525b;

}
@media only screen and (max-width: 1024px) {
  .home {
    gap: 6rem;
  }
}
@media only screen and (max-width: 900px) {
  .home {
    gap: 1rem;
  }
}
@media only screen and (max-width: 760px) {
  .home {
    flex-direction: column-reverse;
  }
}

.home::after {
  content: "";
  height: 350px;
  width: 250px;
  position: absolute;
  bottom: 18%;
  left: 10%;
  background: #b3dee5;
  opacity: 0.25;
  border-radius: 51% 49% 100% 10%/45% 69% 31% 55%;
}
@media only screen and (max-width: 900px) {
  .home::after {
    display: none;
  }
}
.home::before {
  content: "";
  height: 200px;
  width: 200px;
  position: fixed;
  top: 25%;
  left: 40%;
  background: #ffa101;
  opacity: 0.2;
  border-radius: 77% 23% 46% 54%/45% 53% 47% 55%;
}
@media only screen and (max-width: 900px) {
  .home::before {
    display: none;
  }
}
.home .catch-phrase {
  color: #333333;
  position: relative;
}
.home .catch-phrase h1 span {
  background: linear-gradient(to right, #ffa101 0, #31525b 10%, #ffa101);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: shine 10s infinite ease-in;
  font-size: 2,5rem;
}
@keyframes shine {
  0% {
    background-position: left;
  }
  60% {
    background-position: 500px;
  }
  100% {
    background-position: right;
  }
}
.home .catch-phrase span {
  color: #322c21;
  text-align: justify;
}
.home .catch-phrase .buttons {
  margin: 2rem 0;
  display: flex;
  gap: 2rem;
}
.home .catch-phrase .buttons button {
  height: 2rem;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  padding: 0 1rem;
  color: white;
  font-size: 0.9rem;
  z-index: 1;
}
.home .catch-phrase .buttons .hire-me {
  background: #31525b;
  border: solid 1px transparent;
  transition: all 0.5s;
}
.home .catch-phrase .buttons .hire-me:hover {
  color: #31525b;
  border-style: solid;
  border-width: 1px;
  border-color: rgba(0, 0, 0, 0.145);
  border-radius: 3px;
  -webkit-border-radius: 3px;
  background: #f5f5f5;
}
.home .catch-phrase .buttons .cv {
  background: #31525b;
  color: white;
  border-style: solid;
  border-width: 1px;
  border-color: rgba(0, 0, 0, 0.145);
  border-radius: 3px;
  -webkit-border-radius: 3px;
  transition: all 0.5s;
  z-index:1;
}
.home .catch-phrase .buttons .cv:hover {
  background: white;
  border-color: #31525b;
  color: #31525b;
}
.maibelline{
  border-radius: 30% 30% 30% 30%;
  height:30rem;  
}
.home .profil {
  position: relative;
  background-color:#ECF7F8;
  border-radius:50% 10% 40% 40%;
  z-index: -20;
}

@media only screen and (max-width: 900px) {
  .maibelline {
    height: 19rem;
  };
}
.a {
  z-index: 1;
}

